.logo {
  align-items: center;
  display: flex;
}

.logo__icon {
  animation: animationLogo 3s 1s infinite;
}

@media screen and (min-width: 768px) {
  .logo__icon {
    margin-right: 8px;
  }
}

.logo__text {
  color: #fff;
  font-size: 30px;
  line-height: 35px;
}

.logo__text:hover {
  animation: animationLogoText 2s infinite;
  -webkit-mask-image: linear-gradient(-75deg, rgba(0, 0, 0, .6) 30%, #000 50%, rgba(0, 0, 0, .6) 70%);
  -webkit-mask-size: 200%;
}

@media screen and (max-width: 767.98px) {
  .logo__text {
    display: none;
  }
}

@keyframes animationLogo {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.25);
  }

  100% {
    transform: scale(1);
  }
}

@-webkit-keyframes animationLogoText {
  from {
    -webkit-mask-position: 150%;
  }

  to {
    -webkit-mask-position: -50%;
  }
}

.nav__list {
  align-items: center;
  display: flex;
}

.nav__item {
  margin-right: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1);
}

.nav__item:hover, .nav__item:focus {
  transform: scale(1.15);
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #fff;
  padding: 15px 0;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.nav__link:hover, .nav__link:focus {
  color: #a1a1a2;
}

.nav__link--current:after {
  width: 100%;
  height: 3px;
  content: "";
  background: #ff001b;
  display: block;
  position: absolute;
  bottom: 12px;
  left: 0;
}

.form-group {
  width: 280px;
  justify-content: center;
  margin: 0 auto 22px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .form-group {
    width: 336px;
    margin-bottom: 20px;
  }
}

@media screen and (min-width: 1280px) {
  .form-group {
    width: 394px;
  }
}

.form-group__input {
  width: 100%;
  height: 20px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-bottom: .5px solid #fff;
  outline: none;
  padding: 4px 2px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.142;
  transition: border-bottom .25s cubic-bezier(.4, 0, .2, 1);
}

.form-group__input::-ms-input-placeholder {
  color: #fff;
}

.form-group__input::placeholder {
  color: #fff;
}

.form-group__input:hover, .form-group__input:focus {
  border-bottom: .5px solid #a1a1a2;
}

.form-group__button {
  z-index: 50;
  width: 20px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  position: absolute;
  bottom: 0;
  right: 0;
}

.form-group__icon {
  width: 12px;
  height: 12px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1);
}

.form-group__icon:hover, .form-group__icon:focus {
  stroke: #a1a1a2;
  transform: scale(1.5);
}

.error-notification {
  text-align: center;
