}

#spinner:not([hidden]) {
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

#spinner:after {
  content: "";
  width: 80px;
  height: 80px;
  will-change: transform;
  border: 2px solid #f3f3f3;
  border-top: 3px solid #f25a41;
  border-radius: 100%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.scroll-btn {
  width: 50px;
  height: 50px;
  z-index: 1;
  color: #ff6b08;
  background-color: #ff6b08;
  border-radius: 50%;
  padding: 0;
  transition: color .3s ease-in-out;
  animation-name: up;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: fixed;
  bottom: 100px;
  right: 50px;
  box-shadow: 8px 7px 7px -2px rgba(0, 0, 0, .49);
}

@keyframes up {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}

@media screen and (min-width: 320px) {
  .scroll-btn {
    width: 30px;
    height: 30px;
    bottom: 50px;
    right: 30px;
  }
}

@media screen and (min-width: 768px) {
  .scroll-btn {
    width: 40px;
    height: 40px;
    bottom: 70px;
  }
}

.scroll-btn:hover {
  background-color: #fff;
  box-shadow: 8px 7px 7px -2px rgba(0, 0, 0, .49);
}

.scroll-btn:hover .scroll-img {
  fill: #ff6b08;
}

.scroll-btn:active {
  transform: translateY(-1px);
  box-shadow: 6px 9px 22px -1px rgba(0, 0, 0, .73);
}

.scroll-btn:active .scroll-img {
  fill: #ff6b08;
}

.scroll-img {
  fill: #fff;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.page-action {
